import { SecurityContext } from "../services/api/droppableService";
import { initWidget } from "./widget";

const widgetEntry = process.env.WIDGET_ENTRY || "revisionFloat"; // Default to revisionFloat
const response = await SecurityContext();
const securitycontext = response.securitycontextpreference;
const email = response.email;


initWidget(
  (widget) => {
    console.log("[widget-starter] initWidget callback invoked. Widget:", widget);
    widget.setTitle("");
    widget.addPreference(securitycontext); // Security Context Preference
  

    widget.setValue("email", email);
  

    // Add additional preferences as needed
    widget.addPreference({
      key: "emailPreference",
      value: email
    });
    console.log("[widget-starter] Added emailPreference with value:", email);
    // New Boolean preference for Email Notification
    widget.addPreference({
      key: "emailNotification",
      value: true // Set to true or false based on your requirement
    });
  

    if (widgetEntry === "plantAssignment") {
      import("../plantIndex")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing plantIndex", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    } else if (widgetEntry === "massUpload") {
      import("../massuUploadIndex")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing plantIndex", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    } else {
      import("../index")
        .then((module) => {
          module.default();
        })
        .catch((error) => {
          console.error("[widget-starter] Error dynamically importing index", error);
          widget.body.innerHTML = "<div style='color: red;'>Error loading widget content.</div>";
        });
    }
  },
  (error) => {
    console.error("[widget-starter] initWidget encountered an error:", error);
  }
);
